legend {
  overflow: hidden;
  display: inline-block;
  *display: inline;
  zoom: 1;
  text-indent: -9999px;
  white-space: nowrap;
  line-height: 0;
  width: 0;
  height: 0;
  font-size: 0;
}

.clearFix {
  clear: both;
}

.clearFix:after {
  content: " ";
  display: table;
}

.clearFix:after {
  clear: both;
}

.icon {
  position: relative;
  overflow: hidden;
  display: inline-block;
  *display: inline;
  zoom: 1;
  text-indent: -9999px;
  white-space: nowrap;
  line-height: 0;
  vertical-align: middle;
}

.txtHidden {
  overflow: hidden;
  display: inline-block;
  *display: inline;
  zoom: 1;
  text-indent: -9999px;
  white-space: nowrap;
  line-height: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* reset */
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, textarea, p, blockquote, th, td, input, select, textarea, button {
  margin: 0;
  padding: 0;
}

fieldset, img {
  border: 0 none;
}

dl, ul, ol, menu, li {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

input, select, textarea, button {
  vertical-align: middle;
}

button {
  border: 0 none;
  background-color: transparent;
  cursor: pointer;
}

body {
  background: #fff;
}

body, th, td, input, select, textarea, button {
  font-family: "NotoSans", Helvetica, "맑은 고딕", sans-serif;
} /* color값은 디자인가이드에 맞게사용 */
a {
  color: #333;
  text-decoration: none;
}

i {
  font-style: normal;
}

a:active, a:hover {
  text-decoration: none;
}

address, caption, cite, code, dfn, em, var {
  font-style: normal;
  font-weight: normal;
}

h1, h2, h3, h4, h5 {
  font-family: "NotoSans", Helvetica, "맑은 고딕", sans-serif;
  font-weight: 400;
}

input {
  -webkit-border-radius: 0;
  -webkit-appearance: none;
}

[tabindex]:focus, a:focus, textarea:focus, button:focus, input:focus {
  outline: none;
}

pre {
  font-size: 16px;
  font-family: "NotoSans", Helvetica, "맑은 고딕", sans-serif;
  font-weight: 400;
  white-space: normal;
}

