body,
html {
  width: 100%;
  height: 100%;
  background-color: #111414;
  color: #efefef;
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: inherit;
  border: none;
  box-shadow: none;
  border-radius: 0;
  overflow: visible;
  color: inherit;
  cursor: pointer;
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  padding: 20px 40px;
  animation: fadeIn 0.5s 2.8s forwards;
  transition: top 0.3s ease-in-out;
  transform: translateY(-100%);
  opacity: 1;
  z-index: 999;
}

.header .logo,
.header .menu-button {
  font-size: 1.125rem;
  font-weight: 700;
}

@keyframes fadeIn {
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.header .logo {
  position: absolute;
  top: 50%;
  left: 40px;
  transform: translateY(-50%);
  transition: all 0.3s ease-in-out;
}

.header .logo.hide {
  top: -100%;
}

.header .menu-button {
  padding: 0px 12px;
  border: 2px solid #fbbf24;
  border-radius: 24px;
}

.header .nav {
  display: none;
  position: absolute;
  top: 74px;
  right: 0;
  text-align: center;
}

.header .nav .menu_list {
  display: flex;
  flex-direction: column;
  text-align: right;
}

.header .nav .menu_list .menu {
  opacity: 0;
  position: relative;
  right: -100px;
  font-size: 1.125rem;
  padding: 6px 40px;
  overflow: hidden;
}

.header .nav .menu_list .menu:hover {
  transform: rotate(26deg);
}

.header .nav .menu_list .menu:hover a {
  background-color: #c084fc;
}

.header .nav .menu_list .menu a {
  height: auto;
  padding: 0px 12px;
  border: 2px solid #fbbf24;
  background-color: #111414;
  border-radius: 24px;
}

.main,
footer {
  display: flex;
  align-items: center;
  flex-direction: column;
}

section {
  width: calc(100vw - 300px);
}

.main {
  gap: 160px;
  padding-top: 100px;
  overflow: hidden;
}

.main section .section-title {
  position: relative;
  padding: 32px 16px;
  font-size: 2rem;
  color: #fbbf24;
}

.main section .section-title sup {
  font-size: 0.875rem;
}

.main section .section-title:before {
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fbbf24;
  transform: scaleX(0);
  transform-origin: left center;
  transition: transform 0.5s ease-out;
  content: "";
}

.main section .section-title.animate-border:before {
  transform: scaleX(1);
}

.main section.intro {
  position: relative;
  height: calc(100dvh - 100px);
}

.main section.intro .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main section.intro .content .main-title {
  position: absolute;
  font-size: 4.5rem;
  opacity: 0;
}

.main section.intro .content .main-title.from-top {
  top: 19%;
  transform: translateY(-200%) scale(3);
  animation: mainTitleTop 1.5s forwards;
}

.main section.intro .content .main-title.from-bottom {
  bottom: 30%;
  transform: translateY(200%) scale(3);
  animation: mainTitleBottom 1.5s forwards;
}

@keyframes mainTitleTop {
  65% {
    opacity: 1;
    transform: translateY(140px) scale(1);
  }
  75% {
    opacity: 1;
    transform: translateY(140px) scale(1);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
@keyframes mainTitleBottom {
  65% {
    opacity: 1;
    transform: translateY(-140px) scale(1);
  }
  75% {
    opacity: 1;
    transform: translateY(-140px) scale(1);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
.main section.intro .content .img-box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);
  height: 0;
  width: 260px;
  border-radius: 24px;
  animation: introImgBox 0.8s 1.2s forwards ease-in-out;
  background-size: cover;
  overflow: hidden;
}

.main section.intro .content .img-box .intro-img {
  max-width: 260px;
  max-height: 260px;
  min-width: 220px;
  min-height: 220px;
  background: url(../images/intro.png) center center no-repeat;
  background-size: cover;
  border-radius: 24px;
}

@keyframes introImgBox {
  100% {
    height: 260px;
  }
}
.main section.intro .content .sub-title {
  position: absolute;
  font-size: 1.125rem;
  color: hsla(0, 0%, 100%, 0.6);
  text-align: center;
  opacity: 1;
}

.main section.intro .content .sub-title.first {
  bottom: 22%;
  animation: fadeIn 1s 2s forwards;
  opacity: 0;
  transform: translateY(20px);
}

.main section.intro .content .sub-title.second {
  opacity: 0;
  bottom: 19%;
  animation: fadeIn 1s 2.3s forwards;
  transform: translateY(20px);
}

.main section.works .works-list .work {
  padding: 40px 16px;
  border-bottom: 1px solid rgba(200, 200, 200, 0.2);
}

.main section.works .works-list .work .info .customer {
  font-size: 1.125rem;
  color: rgba(255, 255, 255, 0.6);
}

.main section.works .works-list .work .info .title {
  font-size: 3.8rem;
}

.main section.works .works-list .work .desc {
  display: flex;
  justify-content: space-between;
  padding-top: 24px;
}

.main section.works .works-list .work .desc .keywords {
  font-size: 1.125rem;
}

.main section.works .works-list .work .desc .keywords .kinds {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 300;
}

.main section.works .works-list .work .desc .keywords .kinds img {
  vertical-align: middle;
}

.main section.works .works-list .work .desc .keywords .kinds:last-of-type {
  position: relative;
  padding-left: 20px;
  border: none;
}

.main section.works .works-list .work .desc .keywords .kinds:last-of-type::before {
  position: absolute;
  left: 6px;
  top: 50%;
  transform: translateY(-50%);
  content: "|";
}

.main section.works .works-list .work .desc .period {
  font-size: 1.25rem;
  font-weight: 300;
  color: rgba(239, 239, 239, 0.6823529412);
}

.main section.education .flex-wrap {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 80px;
  padding: 60px 16px;
}

.main section.education .flex-wrap .group.g1 {
  flex-basis: auto;
}

.main section.education .flex-wrap .group.g1 .edu-title {
  font-size: 3.8rem;
  line-height: 1.5;
  margin-bottom: 30px;
}

.main section.education .flex-wrap .group.g2 {
  display: flex;
  flex-direction: column;
  gap: 24px;
  flex-basis: 50%;
}

.main section.education .flex-wrap .group.g2 h4 {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.8);
}

.main section.education .flex-wrap .group.g2 .item {
